<template>
  <div class="topic" v-if="topic">
    <div class="topic-head">
      <div class="head-top">
        <el-tag v-if="topic.top || topic.good" type="success" effect="dark"  size="mini">{{ topic.top ? "置顶" : "精华" }}</el-tag>
        <h3>{{ topic.title }}</h3>
        <el-button @click="collect" v-if="accesstoken" type='success'>{{topic.is_collect ? '取消收藏' : '收藏'}}</el-button>
      </div>
      <p>
        <span class="iconfont icon-dian disc"></span>
        <span>发布于 {{ topic.create_at | fromNow}}</span>
        <span class="iconfont icon-dian disc"></span>
        <span>作者 {{ topic.author.loginname}}</span>
        <span class="iconfont icon-dian disc"></span>
        <span>{{ topic.visit_count}} 次浏览 </span>
        <span class="iconfont icon-dian disc"></span>
        <span>来自 {{ topic.tab | tabTextTransform}}</span>
      </p>
    </div>
    <div class="topic-content" v-html="topic.content"></div>
    <div class="comment-box" v-if="topic.replies.length">
      <div class="comment-head">
        <span><span>{{ topic.replies.length }}</span> 回复</span>
      </div>
      <div class="comment-list">
        <div v-for="(comment, index) in topic.replies" :key="comment.id" :class="['comment-item',{active: commentLikeMost.includes(comment.id)}]">
          <img :src="comment.author.avatar_url" alt="" />
          <div class="comment-content">
            <span class="info">
              <span>
                {{comment.author.loginname}} {{index +1}}楼 <span class="iconfont icon-dian disc"></span> {{comment.create_at | fromNow}}
              </span>
              <span class="like" v-if="accesstoken || comment.ups.length">
                <span @click="like(comment)" :class="['icon-zans','iconfont', {up: comment.is_uped}]"></span>
                <span v-if="comment.ups.length">{{ comment.ups.length }}</span>
              </span>
            </span>
            <span class="text" v-html="comment.content"></span>
          </div>
        </div>
      </div>
    </div>
      <BasePanel v-if="accesstoken">
        <template #panel-head> 添加回复 </template>
        <template #panel-content>
          <textarea class="comment-ask" v-model.trim="commentText"></textarea>
          <el-button type='primary' @click="reply">回复</el-button>
        </template>   
      </BasePanel>
  </div>
</template>

<script>
import BasePanel from '../components/layout/BasePanel.vue';
import { de_collected, getTopic,collected,zan,reply} from "../plugins/api";
export default {
  components: { BasePanel },
  props: ["topicId"],
  data() {
    return {
      topic: null,
      accesstoken: '',
      commentText:''
    };
  },
  computed: {
    // 文章评论数组中点赞前两名的评论
    commentLikeMost() {
      if(this.topic.replies.length === 0){
        return [];
      }
      const arr = this.topic.replies.slice(0).sort(
        (a, b) => b.ups.length - a.ups.length
      );
      // 存 id 的
      const commentIds = [];

      const maxOne = arr[0].ups.length;
      if (maxOne > 2){
        const oneArr = arr.filter((item) => item.ups.length === maxOne);
        oneArr.forEach((comment) => {
          commentIds.push(comment.id)
        });
        const maxTwo = arr[oneArr.length].ups.length;
        if (maxTwo > 2){
          const twoArr = arr.filter((item) => item.ups.length === maxTwo);
          twoArr.forEach((comment) => {
            commentIds.push(comment.id)
          });
        }
      }

      return commentIds
    }
  },
  async created() {
    const accesstoken = sessionStorage.getItem('accesstoken')
    if(accesstoken){
      this.accesstoken = accesstoken
    }
    const res = await getTopic(this.topicId, accesstoken || null );
    this.topic = res.data;
  },
  methods: {
    async collect() {
      const accesstoken = this.accesstoken
      if(this.topic.is_collect){
        // 取消收藏
        await de_collected(this.topic.id,accesstoken)
        this.topic.is_collect = false
      }else{
        // 收藏
        await collected(this.topic.id,accesstoken)
        this.topic.is_collect = true
      }
    },
    async like(comment){
      // 不能对自己的评论点赞
      const accesstoken = this.accesstoken
      if(accesstoken){
        const res= await zan(comment.id ,accesstoken)
        const id = JSON.parse(sessionStorage.getItem('userInfo')).id
        if(res.action === 'up'){
          // 点赞成功
          // 修改对应评论的 is_uped 以及 ups
          comment.is_uped = true
          comment.ups.push(id)
        }else{
          // 取消点赞成功
          // 修改对应评论的 is_uped 以及 ups
          comment.is_uped = false
          comment.ups = comment.ups.filter(item => item !== id)
        }
      }
    },
    async reply(){
      const {commentText, accesstoken, topic} = this
      if(commentText){
        await reply(topic.id, accesstoken, commentText)
        // 评论的请求成功之后 后端返回的信息太少无法用来更新本地 所以我们重新请求整个文章数据来实现更新
        const res = await getTopic(this.topicId, accesstoken);
        this.topic = res.data;
        this.commentText = ''
      }
    }
  },
};
</script>

<style lang='less'>
.topic {
  width: 1400px;
  margin: 0 auto;
  margin-top: 20px;
  border-radius: 3px;
  overflow: hidden;
  min-height: 140vh;
  .topic-head {
    background-color: #fff;
    padding: 20px;
    border-bottom: 1px solid #ccc;
    .head-top{
      display: flex;
      align-items: center;
      h3 {
        margin-left: 20px;
        font-size: 30px;
      }
    }
    p {
      font-size: 12px;
      margin-top: 10px;
      display: flex;
      align-items: center;
      color: #838383;;
      .disc  {
        font-size: 18px;
        margin-top: 1px;
      }
    }   

  }
  .topic-content {
    padding: 20px;
    line-height: 1.5;
    background-color: #fff;
    img {
      width: 100%;
    }
  }
  li {
    margin-left: 15px;
  }
  .comment-box{
    margin-top: 20px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
    .comment-head{
      background-color: #ccc;
      line-height: 40px;
      padding-left: 10px;
    }
    .comment-list{
      .comment-item{
        display: flex;
        align-items: flex-start;
        border-bottom: 1px solid #ccc;
        padding: 10px 10px 20px;
        img{
          width: 40px;
          margin-right: 15px;
          border-radius: 5px;
        }
        .comment-content {
          display: flex;
          flex-grow: 1;
          flex-direction: column;
          .info {
            display: flex;
            justify-content: space-between;
            .up{
              color: red;
            }
          }

        }
      }
      .comment-item.active{
        background-color: #f4fcf0;;
      }
    }
  }
  .comment-ask{
      width: 400px;
      height: 200px;
  }
}
</style>